import styled from 'styled-components'

export const Main = styled.div`
  width: 36vw;
  position: relative;
  .blur {
    position: absolute;
    width: 100%;
    height: 100%;
    background-image: url(/static/images/home/pope.png);
    background-repeat: no-repeat;
    background-size: 100vw auto;
    background-position: 0px 27%;
    filter: url(/media/blur.svg#blur);
    filter: blur(20px);
  }
  .opinion {
    padding: 30px 60px 30px 30px;
    color: white;
    font-size: 16px;
    background-color: #424242a3;
    z-index: 100;
    position: relative;
    &__what{
      margin: 20px 0px 0px;
      font-weight: bold;
      font-size: 1.3em;;
    }

    &__description {
      font-weight: 200;
      font-size: 1.3em;;
    }

    &__more-info {
      margin: 20px 0px;
      font-size: 0.85em;
      text-decoration: underline;
      text-underline-position: under;
      font-weight: 400;
      color: rgba(255, 255, 255, .9);
      .wiki {
        height: 12px;
        margin-right: 5px;
      }
    }

    &__title {
      font-size: 3.6em;
      font-weight: 400;
      margin: 0px 0px 20px;
    }

    &__second-title {
      color: rgba(255, 255, 255, .8);
    }
  }

  .actions {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    z-index: 100;
    position: relative;
  }
`;



